* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;

}
.container {
  padding: 0 1rem;
}
.btn {
  display: inline-block;
  border: none;
  background-color: rgb(3, 3, 3);
  padding: 5px 10px;
  color:white;
}
.btn:hover {
  background: grey;
}